import * as React from "react";
import PushPerformanceDimensionTableRowPresentation from "./PushPerformanceDimensionTableRowPresentation";
import {BPanel, Col, DropdownButton, Icon, MenuItem, Table} from "@sketchpixy/rubix/lib/index";

export default class PushPerformanceDimensionTablePresentation extends React.Component{

    render(){
        let context=this.props;
        return(
            <BPanel>
            <div className="tablehead">
                 <h4 className="pull-left">Push Notification By {this.props.dimensionSelection.dimensionKeyLabel}</h4>
                 <div className="pull-right">
                      <span className="dropdown-autoWidth">
                        <DropdownButton bsStyle='darkcyan' title={this.props.dimensionSelection.dimensionKeyLabel} id='dropdown-success' onSelect={this.props.selectAggregator}>
                            <MenuItem eventKey="City">City</MenuItem>
                            <MenuItem eventKey="State">State</MenuItem>
                            <MenuItem eventKey="Country">Country</MenuItem>
                        </DropdownButton>
                      </span>
                    <Icon glyph="glyphicon-info-sign" className="rightIcon pull-right"/>
                 </div>
            </div>
                <Table responsive className="responsive rounded collapsed" hover={false} bordered={false} >
                    {!this.props.isLoading &&
                        <tbody>
                            <tr>
                                {
                                    this.props.dimensionPerformance.map(function (data, index) {
                                        return <PushPerformanceDimensionTableRowPresentation cellData={data}
                                                                                             color={context.colors[index]}/>
                                    })
                                }
                            </tr>
                        </tbody>
                    }
                    {this.props.isLoading &&
                    <tbody>
                    <tr>
                        <td colSpan={5} style={{textAlign:"center"}}>
                            Loading....
                        </td>
                    </tr>
                    </tbody>}
                </Table>
            </BPanel>
        );
    }
}


