import * as React from "react";
import AuthorPerformanceContainer from "./authorPerformance/AuthorPerformanceContainer";
import {connect} from "react-redux";
import {Col} from "@sketchpixy/rubix/lib/index";
import PushNotificationPerformanceContainer from "./pushNotificationPerformance/PushNotificationPerformanceContainer";
import PublishedVsViewedContainer from "./publishedVsViewed/PublishedVsViewedContainer";
import CMSTopDimensionContainer from "./cmsTopDimensionViews/CMSTopDimensionContainer";
import PushPerformanceDimensionContainer from "./pushNotificationPerformanceUserDimension/PushPerformanceDimensionContainer";
import TopStoryCompactViewContainer from "./topStoriesAnalytics/TopStoryAnalyticsContainer";
import * as objectHash from "object-hash";
import moment from "moment-timezone";
import UserDeviceReferralAnalyticsContainer from "./userDeviceReferralAnalytics/UserDeviceReferralAnalyticsContainer";
import DimensionPageViewTrendContainer from "./dimensionPageViewTrend/DimensionPageViewTrendContainer";
import PushNotificationPerformanceCMSContainer from "./pushNotificationPerformanceCMSDimension/PushNotificationPerformanceCMSContainer";

const mapStateToProps = (state) => {


    return {
        headerFilter: state.headerFilter,
        user: state.user
    }
};

@connect(mapStateToProps)
export default class DashboardComponent extends React.Component {

    getHash=(objectToHash)=>{
        return objectHash.sha1(objectToHash);
    };

    render() {
        let headerFilter=this.props.headerFilter;
        headerFilter["propertyId"]=this.props.user.propertyId;
        let key = this.getHash(headerFilter);
        return (
                    <div>
                        <Col md={7} className ="dashboard_leftView">
                            {/*<UserDeviceReferralAnalyticsContainer/>*/}
                            <div className="mb-1">
                                <TopStoryCompactViewContainer headerFilter={this.props.headerFilter} key={key} user={this.props.user}/>
                            </div>
                            <div className="mb-1">
                                <DimensionPageViewTrendContainer headerFilter={this.props.headerFilter} key={key} user={this.props.user}/>
                            </div>
                        </Col>
                        <Col md={5}>
                            <div className="mb-1">
                                <PublishedVsViewedContainer headerFilter={this.props.headerFilter} key={key} user={this.props.user}/>
                            </div>
                            <div className="mb-1">
                                <PushNotificationPerformanceContainer headerFilter={this.props.headerFilter} key={key} user={this.props.user}/>
                            </div>
                            <div className="mb-1">
                                <PushPerformanceDimensionContainer headerFilter={this.props.headerFilter} key={key} user={this.props.user}/>
                            </div>
                            <div className="mb-1">
                                <AuthorPerformanceContainer headerFilter={this.props.headerFilter} key={key} user={this.props.user}/>
                            </div>
                            <div className="mb-1">
                                <CMSTopDimensionContainer headerFilter={this.props.headerFilter} key={key} user={this.props.user}/>
                            </div>
                        {/*<div className="mb-1">
                         <PushNotificationPerformanceCMSContainer/>
                         </div>*/}
                        </Col>
                    </div>


        );
    }

}


