import * as React from "react";
import {Icon} from "@sketchpixy/rubix/lib/index";
import humanFormat from 'human-format';

export default class RegisteredVsActivePresentation extends React.Component{

    render(){
        return(
            <div className="text-center blockSpan">
                <div className="text-center"><Icon glyph="icon-fontello-laptop"  className="icon_large"/></div>
                <p className="textSize">Active Devices</p>
                <p>{humanFormat(Math.floor(this.props.activeDevices))}</p>
            </div>
        );
    }

}


