import * as React from "react";
import {Icon} from "@sketchpixy/rubix/lib/index";
import PushNotificationOpenRateSparkline from "./PushNotificationOpenRateSparkline";
import humanFormat from 'human-format';

export default class PushNotificationOpenVsSentPresentation extends React.Component{

    render(){
        return(
            <row >
            <div className="flexBox">
                <div className="flexItem">
                    <div className="text-center"><Icon className="icon_large" glyph="icon-fontello-paper-plane-2" style={{color:"#F9969F"}}/></div>
                    <p className="text-center smallFont">  {humanFormat(Math.floor(this.props.totalReach))}</p>
                </div>
                <div className="flexItem">
                    <div className="text-center"><Icon className="icon_large" glyph="icon-simple-line-icons-envelope-open" style={{color:"#3CAA96"}}/></div>
                    <p className="text-center smallFont">{humanFormat(Math.floor(this.props.totalOpen))}</p>
                </div>
            </div>
            <div>
                 <PushNotificationOpenRateSparkline data={this.props.openRateArray}/>
            </div>
            </row>
        );
    }

}


