import * as React from "react";
import {BPanel, Icon, OverlayTrigger, Popover, Table} from "@sketchpixy/rubix/lib/index";
import AuthorPerformancePresentationTableRow from "./AuthorPerformancePresentationTableRow";

export default class AuthorPerformancePresentationTable extends React.Component {

    render() {

        return (
            <BPanel>
                <Table responsive className=" rounded collapsed" hover={false} bordered={false} radius="2px">
                    <thead className="tableHeadBackground">
                    <tr>
                        <th style={{borderBottom: "0px"}}><h4>Top Author</h4></th>
                        <th style={{borderBottom: "0px"}}>API</th>
                        <th style={{color: "#998282", borderBottom: "0px"}}><Icon glyph="glyphicon-edit"/></th>
                        <th style={{color: "#3CAA96", borderBottom: "0px"}}><Icon glyph="glyphicon-star"/></th>
                        <th style={{color: "#F0FFFF", borderBottom: "0px", textAlign: "right"}}>
                            <OverlayTrigger trigger={["hover", "focus"]} placement="left"
                                            overlay={<Popover id='popover-top-1' title="Author Performance Index">
                                                This widget gives an overview of the author's performance in terms of
                                                the articles published and the views that he gets.
                                            </Popover>}>
                                <Icon glyph="glyphicon-info-sign rightIcon"/>
                            </OverlayTrigger>
                        </th>
                    </tr>
                    </thead>
                    {!this.props.isLoading &&
                        <tbody>
                        {
                            this.props.data.map((authorPerformance, index) => (
                                <AuthorPerformancePresentationTableRow
                                    data={authorPerformance} key={authorPerformance.articleAuthor}
                                    color={this.props.colors[index]}
                                    authorPageViewsData={this.props.authorPageViewsData.authorPageViewsDataArray ? this.props.authorPageViewsData.authorPageViewsDataArray[authorPerformance.articleAuthor] : []}
                                    /> )
                            )}
                        </tbody>
                    }
                    {this.props.isLoading &&
                        <tbody>
                            <tr>
                                <td colSpan={5} style={{textAlign:"center"}}>
                                    Loading....
                                </td>
                            </tr>
                        </tbody>}
                </Table>
            </BPanel>



        );
    }

}



