import React from 'react';
import {Route} from 'react-router';
import DashboardComponent from "./dashboard/DashboardComponent";
import PushNotificationPerformanceContainer from "./dashboard/pushNotificationPerformance/PushNotificationPerformanceContainer";
import AuthenticationContainer from "./login/AuthenticationContainer";
import Login from "./login/Login";

const routeWithSidebar = (
    <Route component={AuthenticationContainer}>
        <Route path="/"  component={DashboardComponent}/>
        <Route path="/dashboard" component={DashboardComponent}/>
        <Route path="/pushPerformance" component={PushNotificationPerformanceContainer}/>
        <Route path="/login" component={Login}/>
    </Route>
);

export default (
    <Route>
        <Route>
            {routeWithSidebar}
        </Route>

    </Route>
);



