import React from "react";
import {connect} from "react-redux";
import {Col, Grid, MainContainer, Row} from "@sketchpixy/rubix";
import Header from "../common/header";
import Sidebar from '../common/sidebar';
import {authenticated, setRedirectUrl, updateUserData} from "./redux/loginAction";
import Login from "./Login";
import DashboardComponent from "../dashboard/DashboardComponent";

const mapStateToProps = (state, ownProps) => {
    return {
        isLoggedIn: state.user ? state.user.isLoggedIn : false,
        currentURL: ownProps.location.pathname
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
        authenticated: (isAuthenticated) => {
            dispatch(authenticated(isAuthenticated));
        },
        updateUserData: (user) => {
            dispatch(updateUserData(user))
        },
        setRedirectUrl: (url) => {
            dispatch(setRedirectUrl(url))
        },
        isLoggedInAction: (loginData) => {
            dispatch({type: "IS_LOGGED_IN", loginData: loginData})
        }
    };
};

@connect(mapStateToProps, mapDispatchToProps)
export default class AuthenticationContainer extends React.Component {
    componentWillMount() {
        const {currentURL, isLoggedIn} = this.props;
        console.log("Checking for valid login AuthenticationContainer: ", currentURL);

        if (!isLoggedIn) {
            let hash = document.cookie.replace(/(?:(?:^|.*;\s*)Auth\s*\=\s*([^;]*).*$)|^.*$/, "$1");
            console.log("authhash in authentication container", hash);
            this.props.isLoggedInAction({authHash: hash, onSuccessRedirect: currentURL, onFailureRedirect: "/login"});
        }
    }

    render() {
        let hash = document.cookie.replace(/(?:(?:^|.*;\s*)Auth\s*\=\s*([^;]*).*$)|^.*$/, "$1");

        let renderComponent = {};
        if (hash && this.props.currentURL === "/login") {
            renderComponent = <DashboardComponent/>;
        }
        else
            renderComponent = this.props.children;
        let xml = <MainContainer {...this.props}>
            <Sidebar />
            <Header />
            <div id='body'>
                <Grid>
                    <Row>
                        <Col xs={12}>
                            {renderComponent}
                        </Col>
                    </Row>
                </Grid>
            </div>
        </MainContainer>;

        if (!hash) {
            return (
                <Login/>
            )
        }
        return xml;
    }
}


