import * as React from "react";
import {BPanel, Icon, Table} from "@sketchpixy/rubix/lib/index";
import humanFormat from "human-format";

export default class PublishedVsViewedPresentation extends React.Component {

    render() {
        return (
            <BPanel className="p-0 published_view">
            <Icon glyph="glyphicon-info-sign" className="rightIcon pull-right"/>
            <div  className="pt-2 pb-1 p-1">
                {!this.props.isLoading && <div className="flexBox">
                    <div className="flexItem">
                        <Icon style={{color: "#fa96a0"}} glyph="glyphicon glyphicon glyphicon-edit bigFont "/>
                        <span className="bigFont WhiteColor"> {this.props.data.published?humanFormat(this.props.data.published):'No Data'}</span>
                        <span className="smallFont mobileBlock"> Stories Published</span>
                    </div>
                    <div className="flexItem">
                        <Icon style={{color: "#286a62"}} glyph="glyphicon glyphicon-file bigFont "/>
                        <span className="bigFont WhiteColor"> {this.props.data.pageViews?humanFormat(this.props.data.pageViews):'No Data'}</span>
                        <span className="smallFont mobileBlock"> Page Views</span>
                    </div>
                </div>}
                {this.props.isLoading && <div>Loading....</div>}
            </div>
            </BPanel>

        )
            ;
    }


}



